* {
  transition: 0.5s;
}
header {
  width: 100%;
  height: 700px;
  background-image: linear-gradient(to left top, orange, orange 50%, rgb(255, 255, 255) 50%, rgb(255, 255, 255));
  background-size: cover;
  background-repeat: no-repeat;
}
.headerLeftSide,
.headerRightSide {
  height: 100%;
  width:50%
}
.headTitle{
  width: 75%;;
}
@media(max-width:768px){
  .headTitle{
    width: 100%;
  }
    .headerLeftSide{
        height:250px !important;
        width:85% !important;
    }
    .headerRightSide{
        height:300px !important;
    }
    .container{
        width:250px !important;
        height:300px !important;
    }
    .headTitle p{
        font-size:3.5vw !important;
    }
}
@media(min-width:525px) and (max-width:768px) {
  .headTitle p {
    font-size: 2vw !important;
  }
}
@media(min-width: 525px) and (max-width: 767px){
.container{
    height:320px !important;
}
}

[alt="HeaderImg"] {
  object-fit: cover;
  border-radius: 20px;
}
.headTitle{
    border:1px solid grey;
    padding:5px;
    border-radius:10px;
}
.headTitle p{
    font-size:2vw;
}
.star{
    font-size:16px
}

.imgContainer {
  width: 300px;
  border-radius: 20px;
}
.gifContainer {
  width: 75px;
  height: 75px;
 background-position:center;
 background-size:cover;
}
.container {
  position: relative;
  width: 350px;
  height: 500px;
}

.container:active {
  width: 180px;
  height: 245px;
}

#card {
  position: absolute;
  inset: 0;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  background-position: center;
  background-size: cover;
}

.subtitle {
  transform: translateY(160px);
  color: rgb(134, 110, 221);
  text-align: center;
  width: 100%;
}

.title {
  opacity: 0;

  position: absolute;
  font-size: x-large;
  font-weight: bold;
  color: white;
}

.tracker:hover ~ #card .title {
  opacity: 1;
}

#prompt {
  bottom: 8px;
  opacity: 0;
  left: 12px;
  z-index: 20;
  font-size: 20px;
  font-weight: bold;
  transition: 300ms ease-in-out-out;
  position: absolute;
  max-width: 110px;
  color: rgb(255, 255, 255);
}

.tracker {
  position: absolute;
  z-index: 200;
  width: 100%;
  height: 100%;
}

.tracker:hover {
  cursor: pointer;
}

.tracker:hover ~ #card #prompt {
  opacity: 1;
}

.tracker:hover ~ #card {
  filter: brightness(1.1);
}

.container:hover #card::before {
  transition: 200ms;
  content: "";
  opacity: 80%;
}

.canvas {
  perspective: 800px;
  inset: 0;
  z-index: 200;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "tr-1 tr-2 tr-3 tr-4 tr-5"
    "tr-6 tr-7 tr-8 tr-9 tr-10"
    "tr-11 tr-12 tr-13 tr-14 tr-15"
    "tr-16 tr-17 tr-18 tr-19 tr-20"
    "tr-21 tr-22 tr-23 tr-24 tr-25";
}

#card::before {
  content: "";
  filter: blur(2rem);
  opacity: 30%;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}

